// components/TanStackProvider/TanStackProvider.tsx
'use client'; // Цей компонент є обгорткою, яка працює на клієнті

import { QueryClient, QueryClientProvider } from '@tanstack/react-query';
import { ReactQueryDevtools } from '@tanstack/react-query-devtools';
import { useState } from 'react';

interface TanStackProviderProps {
  children: React.ReactNode;
}

const TanStackProvider: React.FC<TanStackProviderProps> = ({ children }) => {
  // Створюємо екземпляр QueryClient.
  // Використання useState гарантує, що QueryClient не буде створюватися заново при кожному рендері.
  const [queryClient] = useState(() => new QueryClient({
    defaultOptions: {
      queries: {
        staleTime: 60 * 1000,
        refetchOnWindowFocus: false,
      },
    },
  })
);

  return (
    // Надаємо доступ до queryClient усім дочірнім компонентам
    <QueryClientProvider client={queryClient}>
      {children}
      {/* Додаємо інструменти розробника для дебагінгу */}
      <ReactQueryDevtools initialIsOpen={false} />
    </QueryClientProvider>
  );
};

export default TanStackProvider;